import React from "react"
import { Link } from "gatsby"
import Head from "../components/head"

const NotFound = () => {
  return (
    <React.Fragment>
      <Head title="404" />
      <h1>Page not found</h1>
      <h3>
        <Link to="/">Take me home!</Link>
      </h3>
    </React.Fragment>
  )
}

export default NotFound
